@mixin respond($bp) {
  @if ($bp == xl) {
    @media only screen and (min-width: 1400px) { @content; }
  }

  @if ($bp == up-xl) {
    @media only screen and (max-width: 1399px) { @content; }
  }

  @if ($bp == lg) {
    @media only screen and (min-width: 1009px) { @content; }
  }

  @if ($bp == tablet) {
    @media only screen and (max-width: 1008px) { @content; }
  }
  
  @if ($bp == mid) {
    @media screen and (max-width: 768px) { @content; }
  }

  @if ($bp == sm) {
    @media screen and (max-width: 485px) { @content; }
  }
}